import { useState } from "react";
import { useHistory } from "react-router-dom";
import { Container, Content, MenuItem } from "./styles";

export default function MenuBar() {

  const options = ['Dashboard', 'Produtos', 'Ofertas', 'Clientes', 'Fornecedores', 'Finan√ßas'];
  const [clickPosition, setClickPosition] = useState(0);
  const history = useHistory();

  function handleActiveMenuItem(option: string, index: number): void {
    setClickPosition(index);
    history.push(option.toLowerCase());
  }

  return (
    <Container>
      <Content>
        {options.map((option, index) => <MenuItem key={index}
          className="menuItem"
          active={clickPosition === index}
          onClick={() => handleActiveMenuItem(option, index)}
        >{option}</MenuItem>)}
      </Content>
    </Container>
  );
}