const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 80vh;
`;

export default function Dashboard() {
  return (
    <Container>
      <div>
        <p>Dashboard</p>
      </div>
    </Container>
  );
}